import tasa from "../../json/monetaria/tasas/tasatasa.json";
import badlar from "../../json/monetaria/tasas/tasabadlar.json";
import plazo from "../../json/monetaria/tasas/tasaplazo.json";
import pases from "../../json/monetaria/tasas/tasapases.json";
import trcmDates from "../../json/monetaria/tasas/tasadates.json";

export default {
      props: {
        minDate: {
            type: String,
            required: false
        },
        chartHeight: {
type: Number,
            required: false
        }},
  data() {
    return {
      chartData: {
        labels: trcmDates,
        datasets: [
          {
            backgroundColor: "rgba(46,120,210,0)",
            label: "Tasa BCRA",
            data: tasa,
            borderColor: "#2E78D2",
            pointRadius: 0,
            borderWidth: 1.5,
          },
          {
            backgroundColor: "rgba(46,120,210,0)",
            label: "Tasa Badlar",
            data: badlar,
            borderColor: "rgba(46,120,210,0.25)",
            pointRadius: 0,
            borderWidth: 1,
          },
          {
            backgroundColor: "rgba(46,120,210,0)",
            label: "Tasa plazo fijo",
            data: plazo,
            borderColor: "rgba(46,120,210,0.25)",
            pointRadius: 0,
            borderWidth: 1,
          },
          {
            backgroundColor: "rgba(46,120,210,0)",
            label: "Tasa Pases Pasivos 1 dia",
            data: pases,
            borderColor: "#7a49a580",
            pointRadius: 0,
            borderWidth: 1.5,
          },
        ],
      },
      chartOptions: {
        responsive: true,
        maintainAspectRatio: false,
        animation: { duration: 0 },
        layout: {
          padding: {
            left: 0,
            right: 0,
            top: 0,
            bottom: 0,
          },
        },
        scales: {
          xAxes: [
            {
              type: "time",
              offset: true,
              position: "bottom",
              gridLines: {
                color: "#F7F5F0",
                zeroLineColor: "#eee",
                drawBorder: false,
                offsetGridLines: false,
                color: "#F7F5F0",
              },
              ticks: { fontColor: "#bbb", fontSize: 13,                min: this.minDate,
 },
              time: {
                tooltipFormat: "DD/MM/YY",
                unit: "year",

              },
            },
          ],
          yAxes: [
            {
              ticks: { fontColor: "#bbb",                callback: function (value, index, values) {
                  return value + "%";
                }, },
              gridLines: {
                color: "#F7F5F0",
                lineWidth: 1,
                                                zeroLineColor: "#F7F5F0",

                drawBorder: false,
              },
              scaleLabel: {
                display: false,
                labelString: "Base 2004 = 100",
                fontColor: "#888",
              },
              position: "right",
            },
          ],
        },
        legend: {
          display: false,
        },
      },
    };
  },
  methods: {
    filteredArray() {
      return this.chartData.labels
        .filter((val, index, arr) => index > arr.length - 7)
        .reverse();
    },
    getVariation(i) {
      var currentNum = this.chartData.datasets[0].data
        .filter((val, index, arr) => index > arr.length - 8)
        .reverse();
      var A = currentNum[i];
      var B = currentNum[i + 1];
      return (((A - B) / A) * 100.0).toFixed(2);
    },
  },
};
